    margin-top: -152px;
  }
  .loading{
    margin-left: -30px;
    margin-top: 30px;
  }
}
.sweet-loading{
  display: block;
  position: absolute;
  left: 50%;
  top: 50%;
  margin-left : -30px;
  margin-top : -30px;
}