// Content helpers
// -------------------------
// Display

.display-inline {
   display: inline !important;
}
.display-block {
   display: block !important;
}
.display-inline-block {
   display: inline-block !important;
}
.display-hidden {
   display: none !important;
}
.display-table-cell {
   display: table-cell !important;
}

// Position

.position-fixed {
   position: fixed !important;
}
.position-relative {
   position: relative !important;
}
.position-absolute {
   position: absolute !important;
}
.position-static {
   position: static !important;
}

.position-top-0 {
   top: 0;
}
.position-right-0 {
   right: 0;
}
.position-bottom-0 {
   bottom: 0;
}
.position-left-0 {
   left: 0;
}

// Edges
.no-edge-top {
   top: 0 !important;
}
.no-edge-bottom {
   bottom: 0 !important;
}
.no-edge-left {
   left: 0 !important;
}
.no-edge-right {
   right: 0 !important;
}

// Cursors
.cursor-pointer {
   cursor: pointer;
}
.cursor-move {
   cursor: move;
}
.cursor-default {
   cursor: default;
}
.cursor-progress {
   cursor: progress;
}
.cursor-not-allowed {
   cursor: not-allowed;
}

// Vertical alignment
.valign-top {
   vertical-align: top !important;
}
.valign-middle {
   vertical-align: middle !important;
}
.valign-bottom {
   vertical-align: bottom !important;
}
.valign-baseline {
   vertical-align: baseline !important;
}
.valign-text-top {
   vertical-align: top !important;
}
.valign-text-bottom {
   vertical-align: text-bottom !important;
}

// Overflow
.overflow-hidden {
   overflow: hidden;
}
.overflow-visible {
   overflow: visible;
}
.overflow-auto {
   overflow: auto;
}
.overflow-scroll {
   overflow: scroll;
}
.overflow-x-scroll {
   overflow: scroll;
}
.overflow-y-scroll {
   overflow: scroll;
}

// Lists
.bullets-inside {
   list-style: inside;
}
.list-style-circle {
   list-style: circle;
}
.list-style-square {
   list-style: square;
}
.list-style-icons {
   padding-left: 10px;
   margin-left: 0;
   list-style: none;
   > li i {
      float: left;
      width: 1em;
      margin: 5px 12px 0 0;
   }
}

// Borders
// -------------------------

// Add borders
.border {
   border: 1px solid;
}
.border-top {
   border-top: 1px solid;
}
.border-bottom {
   border-bottom: 1px solid;
}
.border-left {
   border-left: 1px solid;
}
.border-right {
   border-right: 1px solid;
}

// Border widths

// Med - 2px
.border-2 {
   border-width: 2px !important;
}
.border-top-2 {
   border-top-width: 2px !important;
}
.border-bottom-2 {
   border-bottom-width: 2px !important;
}
.border-left-2 {
   border-left-width: 2px !important;
}
.border-right-2 {
   border-right-width: 2px !important;
}

// Large - 3px
.border-3 {
   border-width: 3px !important;
}
.border-top-3 {
