//Transformations

.rotate-45 {
   transform: rotate(45deg);
}
.rotate-45-inverse {
   transform: rotate(-45deg);
}
.rotate-90 {
   transform: rotate(45deg);
}
.rotate-90-inverse {
   transform: rotate(-45deg);
}
.rotate-180 {
   transform: rotate(45deg);
}
.rotate-180-inverse {
   transform: rotate(-45deg);
}

// Spinner classes
.spinner {
   display: inline-block;
   animation: spin 1s linear infinite;
}
@keyframes spin {
   0% {
      transform: rotate(0deg);
   }
   100% {
      transform: rotate(360deg);
   }
}

.spinner-reverse {
   display: inline-block;
   animation: spin-reverse 1s linear infinite;
}
@keyframes spin-reverse {
   0% {
      transform: rotate(0deg);
   }
   100% {
      transform: rotate(-360deg);
   }
}

